import PropTypes from 'prop-types';
import React, { useEffect, useRef } from 'react';
import videojs from 'video.js';

export const MainSlide = ({ baseUrl }) => {
	const videoRef = useRef(null);
	const getPath = (fileName) => `${baseUrl}/${fileName}`;

	useEffect(() => {
		//  
		const player = videojs('player-id', {
			controls: false, //   
			loop: true,
			autoplay: true,
			preload: 'auto',
			muted: true,  //     iOS
			fill: true,
		});

		//      mute
		setTimeout(() => {
			if (player.muted()) {
				console.log(' ');
			} else {
				console.log(' ');
			}
		}, 500);  // ,  ,   

		const videoElement = videoRef.current;

		//    
		const togglePlay = () => {
			if (player.paused()) {
				player.play();
			} else {
				player.pause();
			}
		};

		//     iOS
		const onUserInteraction = () => {
			//  ,       
			if (player.paused()) {
				player.play();
			}
		};

		//       
		videoElement.addEventListener('click', togglePlay);
		videoElement.addEventListener('touchstart', togglePlay);

		//  
		const handleScroll = () => {
			if (!videoElement) return;
			let videoTop = videoElement.getBoundingClientRect().top;

			if (videoTop < -400) {
				if (!player.paused()) {
					player.pause();
				}
			}
			if (videoTop > -400) {
				if (player.paused()) {
					player.play();
				}
			}
		};

		//    scroll
		window.addEventListener('scroll', handleScroll);

		//    
		return () => {
			if (player) {
				player.dispose();
			}
			window.removeEventListener('scroll', handleScroll);
		};

	}, [baseUrl]);

	return (
		<div className="main-video">
			<div className="main-video__body">
				<div className="main-video__box _img">
					<video
						ref={videoRef}
						id="player-id"
						className="video-js"
						preload="auto"
						muted
					>
						<source src={getPath('img/audio/showreel-1.mp4')} type="video/mp4" />
					</video>
				</div>

				<div className="main-slide__content main-video__content _container">
					<h1 className="main-slide__title el-slidetitle h1_01901">
						<span> </span>
						<span>  </span>
						<span> Media-Group</span>  
					</h1>
					<h2 className="main-slide__text t_01901">
						    ,   
						      
						,      
						  ,  ,  
						.
					</h2>
				</div>
			</div>
		</div>
	);
};

MainSlide.propTypes = {
	baseUrl: PropTypes.string.isRequired,
};
